//font default
@mixin font-default {
    font-family: $fontfamily-default;
    font-size: 1.5;
    letter-spacing: $letter-spacing;
    color: $color-black;
    font-weight: 300;
}
//font title
@mixin font-title{
    font-family: $fontfamily-default;
    font-size: 3.0rem;
    letter-spacing: $letter-spacing;
    color: $color-3;
    text-shadow: $font-shadow;
    font-weight: 700;
}

@mixin font-subheading{
    font-family: $fontfamily-default;
    font-size: 2.0rem;
    letter-spacing: $letter-spacing;
    color: $color-3;
    text-shadow: $font-shadow;
    font-weight: 400;
}

//font text
@mixin font-text{
    font-family: $fontfamily-secondary;
    font-size: 1.25rem;
    letter-spacing: $letter-spacing;
    color: inherit;
    font-weight: 200;
}

//font text-large
@mixin font-text-large{
    font-family: $fontfamily-secondary;
    font-size: 2.0rem;
    letter-spacing: $letter-spacing;
    color: inherit;
    font-weight: 200;
}