.header{
    width: 100%;
    min-height: 7.5vh;
    display: flex;
    justify-content: space-between;
    &__left{
        width: 50%;
        .nav-bars{
            padding: .5rem 0 0 2rem;
            @include font-title;
            i{
                cursor: pointer;
            }
            span{
                cursor: default;
            }
        }
    }

    &__center{
        width: 100%;
    }

    &__right{
        width: 25%;
    }
}
.explore-box{
    width: 100%;
    height: 30vh;
    background-color: $color-white;
    color: $color-gray-4;
    display: flex;
    justify-content: space-evenly;
    transition: ease-in .3s;

    &__wrapper{
        display: flex;
        flex-direction: column;
        gap: 1.5%;
        h3{
            @include font-subheading;
            margin-bottom: 7.5%;
        }
        p{
            @include font-text;
            
        }
    }

}