@import url('https://fonts.googleapis.com/css2?family=Frank+Ruhl+Libre:wght@300;400;500;700&family=Work+Sans:wght@100;200;300;400;500&display=swap');

//Defaut Font weights 300, 400, 500, 600, 700
$fontfamily-default: 'Frank Ruhl Libre', serif;

//Secondary Font Weights 100, 200, 300, 400, 500
$fontfamily-secondary: 'Work Sans', sans-serif;

$letter-spacing: 2px;
$font-shadow: 1px 1px 2px rgba(0, 0, 0, .75);

//colors
$color-1: #17292e;
$color-2: #4e888a;
$color-3: #f19645;
$color-4: #fe2d36;

$color-white: #f8f1d4;
$color-black: #1f1e1b;

$color-gray-1: #d9d3ba;
$color-gray-2: #bab59f;
$color-gray-3: #7c786a;
$color-gray-4: #5d5a50;
